@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";
@import "vendor/libs";
@import "base/fonts";
@import "base/general";
@import "../blocks/modules";

// COMMON STYLES
html {
    font-family: $font-oswald-regular, sans-serif;
    font-size: $text;
}
.block {
    display: flex;
    flex-flow: column wrap;
}
.btn {
    &__wrapper {
        display: flex;
        justify-content: center;
    }
    padding: rem(3) rem(43);
    border: 1px solid $dark-gray;
    background: transparent;
    cursor: pointer;
    text-transform: uppercase;
    color: $white;
}
.soc-media__icon {
    font-size: $text;
}
.title {
    font-size: $title;
    color: $light-black;
    line-height: 30px;
}
.heading {
    font-size: $heading;
    color: $gray;
}
.title,
.heading {
    text-transform: uppercase;
}
.text {
    font-size: $text;
    color: $gray;
}
.nav-text {
    font-size: $nav-text;
    color: $gray;
}
.quote {
    font-size: $quote;
}
.wrapper {
    max-width: 1300px;
    margin: 0 auto;
}
.header,
.main,
.footer {
    max-width: 100%;
}
//
// ABOUT US
//
.about-us {
    padding: rem(175) rem(60);
    font-family: $font-lekton-regular;
    line-height: 18px;
    .caption {
        padding: 0 rem(185) rem(145) rem(185);
        text-align: center;
        .title {
            margin-bottom: rem(40);
            font-weight: 100;
        }
        .text {
            text-transform: none;
            letter-spacing: 0.1em;
            color: $light-black;
        }
    }
    .content {
        display: flex;
        flex-flow: row wrap;
        .article {
            flex-basis: 25%;
            padding: 0 rem(20);
            .subtitle {
                text-transform: uppercase;
                display: flex;
                align-items: center;
                color: $light-black;
                margin-bottom: rem(10);
                &::before {
                    content: "";
                    display: block;
                    width: 5px;
                    height: 15px;
                    background-color: $dark-blue;
                    float: left;
                    margin-right: rem(5);
                }
            }
            .nav-text {
                color: $light-black;
            }
        }
    }
}
//
// TEAM
//
.team {
    display: flex;
    flex-flow: row wrap;
    font-family: $font-lekton-regular;
    .block {
        flex-basis: 25%;
        height: rem(500);
        justify-content: flex-end;
        position: relative;
        &:nth-child(even) {
            text-align: right;
        }
        &:hover .front {
            transform: rotateY(180deg);
        }
        &:hover .back {
            transform: rotateY(360deg);
        }
        .front {
            .subtitle {
                color: $black;
            }
            .nav-text {
                color: $black;
            }
            .soc-media__icon {
                color: $black;
                &:hover {
                    color: $white;
                    transition: all 0.3s ease;
                }
            }
        }
        .back {
            transform: rotateY(180deg);
            .subtitle {
                color: $white;
            }
            .nav-text {
                color: $white;
            }
            .soc-media__icon {
                color: $white;
                &:hover {
                    color: $black;
                    transition: all 0.3s ease;
                }
            }
        }
        .front,
        .back {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            flex-flow: column wrap;
            justify-content: flex-end;
            padding: rem(68) rem(35);
            backface-visibility: hidden;
            transition: transform 1s;
            .title {
                line-height: 24px;
                margin-bottom: rem(10);
                color: $dark-blue;
            }
            .subtitle {
                text-transform: uppercase;
                line-height: 24px;
                margin-bottom: rem(5);
            }
            .nav-text {
                line-height: 14px;
            }
            .soc-media {
                margin-top: rem(10);
            }
        }
    }
    .john {
        .front {
            background-image: url(../img/john_front.jpg);
            background-size: cover;
            background-position: top right;
        }
        .back {
            background-image: url(../img/john_back.jpg);
            background-size: cover;
            background-position: top right;
        }
    }
    .emma {
        .front {
            background-image: url(../img/emma_front.jpg);
            background-size: cover;
            background-position: top right;
        }
        .back {
            background-image: url(../img/emma_back.jpg);
            background-size: cover;
            background-position: top right;
        }
    }
    .ron {
        .front {
            background-image: url(../img/ron_front.jpg);
            background-size: cover;
            background-position: top right;
        }
        .back {
            background-image: url(../img/ron_back.jpg);
            background-size: cover;
            background-position: top right;
            .nav-text {
                color: $black;
            }
            .soc-media__icon {
                color: $black;
                &:hover {
                    color: $white;
                    transition: all 0.3s ease;
                }
            }
        }
    }
    .lara {
        .front {
            background-image: url(../img/lara_front.jpg);
            background-size: cover;
            background-position: top right;
        }
        .back {
            background-image: url(../img/lara_back.jpg);
            background-size: cover;
            background-position: top right;
            .nav-text {
                color: $black;
            }
            .soc-media__icon {
                color: $black;
                &:hover {
                    color: $white;
                    transition: all 0.3s ease;
                }
            }
        }
    }
}
//
// PROCESS
//
.process {
    padding: rem(175) rem(85) rem(165) rem(85);
    line-height: 18px;
    font-family: $font-lekton-regular;
    background-color: $light-gray;
    .caption {
        padding: 0 rem(185) rem(135) rem(185);
        text-align: center;
        .title {
            margin-bottom: rem(40);
            font-weight: 100;
        }
        .text {
            text-transform: none;
            letter-spacing: 0.1em;
            color: $light-black;
        }
    }
    .content {
        display: flex;
        flex-flow: row wrap;
        .block {
            flex-basis: 25%;
            text-align: center;
            flex-flow: column wrap;
            padding: 0 rem(20);
            .icon {
                display: flex;
                justify-content: center;
                align-items: center;
                align-self: center;
                color: $light-blue;
                width: rem(80);
                height: rem(80);
                border: 2px solid $light-blue;
                border-radius: $circle;
                font-size: rem(36);
                margin-bottom: rem(20);
            }
            .subtitle {
                text-transform: uppercase;
                margin-bottom: rem(15);
            }
        }
    }
}
//
// PORTFOLIO
//
.portfolio {
    padding: rem(175) 0 rem(165) 0;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    line-height: rem(18);
    .title {
        font-weight: 100;
    }
    .nav-portfolio {
        padding: rem(35) 0;
        &__link {
            display: inline-block;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            font-family: $font-lekton-regular;
            color: $light-black;
            border-right: 1px solid $light-black;
            padding: 0 rem(10);
            height: rem(16);
            margin-bottom: rem(50);
            &:last-child {
                border-right: none;
            }
            &:hover {
                color: $blue;
            }
        }
    }
    .block {
        display: grid;
        grid-template-columns: repeat(3, 33.333%);
        grid-template-rows: repeat(4, 250px);
        grid-auto-flow: row dense;
        width: 100%;
        margin-bottom: rem(85);
        .portfolio__item {
            width: 100%;
            height: 250px;
            display: none;
            justify-content: center;
            align-items: center;
            text-align: center;
            font-family: $font-lekton-regular;
            letter-spacing: 0.1em;
            line-height: rem(18);
            position: relative;
            transition: all 0.3s ease;
            &:nth-child(-n + 9) {
                display: flex;
            }
            &:hover .front {
                display: none;
            }
            &:hover .back {
                display: flex;
            }
            &--big {
                height: 500px;
                grid-row: span 2;
            }
            .front {
                padding: 0 rem(70);
                .heading {
                    text-transform: uppercase;
                    color: $black;
                    font-weight: 100;
                }
                .nav-text {
                    color: $black;
                }
            }
            .text-right {
                margin: 0 0 0 auto;
            }
            .text-left {
                margin: 0 auto 0 0;
            }
            .back {
                position: absolute;
                display: none;
                width: 100%;
                height: 100%;
                justify-content: center;
                align-items: center;
                top: 0;
                left: 0;
                flex-direction: column;
                .capture {
                    text-align: left;
                    margin-bottom: rem(45);
                    .nav-text {
                        text-transform: uppercase;
                        color: $black;
                        span {
                            color: $dark-blue;
                        }
                    }
                }
                .btn {
                    color: $dark-blue;
                    padding: rem(10) rem(45);
                    font-size: rem(14);
                    &:hover {
                        background-color: $white;
                    }
                }
            }
        }
        .f54759 {
            background: url(../img/portfolio_bg.jpg) no-repeat;
            background-size: cover;
        }
        .e0d7cd {
            background-color: #e0d7cd;
        }
        .e3d3c8 {
            background-color: #e3d3c8;
        }
        .dbdbd3 {
            background-color: #dbdbd3;
        }
        .e6e6e6 {
            background-color: #e6e6e6;
        }
        .c-513c4b {
            background-color: #513c4b;
            .front {
                .capture {
                    .heading,
                    .nav-text {
                        color: $dark-blue;
                    }
                }
            }
        }
        .d6f0ed {
            background-color: #d6f0ed;
        }
        .f2eed4 {
            background-color: #f2eed4;
        }
        .f0d93e {
            background-color: #f0d93e;
        }
    }
    .load-more {
        color: $light-black;
        &:hover {
            color: $light-blue;
        }
    }
}
//
// STATS
//
.stats {
    padding: rem(55) 0 rem(120) 0;
    background-color: $lighter-gray;
    font-family: $font-oswald-regular;
    .title {
        color: $dark-blue;
        font-weight: 100;
        text-align: center;
        margin-bottom: rem(70);
    }
    &__inner {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
        .block {
            text-align: center;
            .media {
                margin-bottom: rem(50);
            }
            .content {
                .text {
                    color: $dark-blue;
                    text-transform: uppercase;
                }
            }
        }
    }
}
//
// BLOG
//
.blog {
    padding: rem(185) rem(80);
    .title {
        font-family: $font-oswald-regular;
        text-align: center;
    }
    .inner {
        font-family: $font-lekton-regular;
        padding-top: rem(100);
        display: flex;
        flex-flow: row wrap;
        .block {
            flex-basis: 25%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            height: rem(340);
            padding: 0 rem(15);
            overflow: hidden;
            .media {
                background-color: #8ddbcb;
                flex-basis: 50%;
            }
            .content {
                &__title {
                    display: block;
                    font-weight: 700;
                    margin-bottom: rem(20);
                    color: $light-black;
                    text-transform: uppercase;
                }
                &__text {
                    height: rem(70);
                    overflow: hidden;
                    text-overflow: ellipsis;
                    line-height: 18px;
                    letter-spacing: 0.1em;
                    color: $light-black;
                }
                &__footer {
                    display: flex;
                    flex-flow: row wrap;
                    justify-content: space-between;
                    padding-top: rem(10);
                    .author {
                        color: $dark-blue;
                        span {
                            font-weight: 700;
                            text-transform: uppercase;
                        }
                    }
                    .link {
                        color: red;
                        transition: all 0.3s ease;
                        &:hover {
                            color: $light-black;
                        }
                    }
                }
            }
        }
        .block--wide {
            flex-basis: 50%;
            .media {
                background-color: $lighter-gray;
            }
        }
    }
}
//
// CLIENTS
//
.clients {
    padding: rem(75) 0;
    background-color: $darker-gray;
    .slider-slick {
        padding: rem(50) 0 rem(80) 0;
        .client {
            display: flex;
            justify-content: center;
        }
    }
    .swiper-pagination {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        width: 98%;
        .swiper-pagination-bullet {
            width: 14px;
            height: 14px;
            background-color: #97c2be;
            border: none;
            border-radius: $circle;
            cursor: pointer;
            margin-right: 10px;
            &:hover {
                background-color: $dark-blue;
            }
        }
        .swiper-pagination-bullet-active {
            background-color: $dark-blue;
        }
    }
}
//
// TWITTER
//
.twitter {
    padding: rem(190) 0;
    font-family: $font-lekton-italic;
    .post {
        display: flex;
        flex-flow: column wrap;
        align-items: center;
        max-width: 50%;
        margin: 0 auto;
        .icon {
            color: $dark-blue;
            font-size: rem(30);
            margin-bottom: rem(40);
        }
        .quote {
            color: #686868;
            text-align: left;
            line-height: 24px;
            margin-bottom: rem(10);
            a {
                color: $darker-blue;
            }
        }
        .time {
            align-self: flex-start;
            display: flex;
            flex-flow: row nowrap;
            align-items: center;
            width: 100%;
            font-size: rem(12);
            color: $dark-red;
            font-family: $font-lekton-regular;
            font-weight: 700;
            &::before {
                content: "";
                width: rem(27);
                height: 1px;
                background-color: $darker-gray;
                margin-right: 3px;
            }
            &::after {
                content: "";
                width: 35%;
                height: 1px;
                background-color: $darker-gray;
                margin-left: 3px;
            }
        }
    }
}
//
// FOOTER
//
.footer {
    display: flex;
    flex-flow: row wrap;
    .contact-form {
        flex-basis: 50%;
        padding: rem(50) rem(85);
        background-image: linear-gradient(90deg, #dfd8c6 0%, #d2cab7 100%);
        font-family: $font-lekton-regular;
        .quote {
            text-transform: uppercase;
            color: #646250;
        }
        .text {
            font-size: rem(12);
            text-transform: uppercase;
            line-height: 14px;
            color: #646250;
        }
        .form {
            padding: rem(45) rem(50) 0 rem(50);
            display: flex;
            flex-flow: column wrap;
            &__input {
                padding: rem(5) 0;
                color: #a6a385;
                border: none;
                border-bottom: 2px solid #a6a385;
                text-transform: uppercase;
                background-color: transparent;
                margin-bottom: rem(30);
            }
            .btn {
                padding: rem(15) rem(45);
                &:hover {
                    background-color: $white;
                    color: $dark-gray;
                }
                &__wrapper {
                    padding-top: rem(20);
                }
            }
        }
    }
    .contacts {
        display: flex;
        flex-direction: column;
        background-color: $yellow;
        padding: rem(115) rem(115) rem(25) rem(115);
        flex-basis: 50%;
        .inner {
            display: flex;
            flex-flow: row wrap;
            justify-content: space-between;
            margin-bottom: rem(80);
            .company-name {
                .title {
                    font-size: rem(24);
                    color: $blue;
                }
            }
            .address {
                display: flex;
                flex-flow: column wrap;
                font-family: $font-lekton-regular;
                .text {
                    white-space: pre-line;
                    font-size: rem(13);
                    line-height: 14px;
                    color: #333;
                    text-transform: uppercase;
                    &:first-child {
                        margin-bottom: rem(15);
                    }
                }
                .mail {
                    color: $dark-blue;
                    &:hover {
                        color: $white;
                    }
                }
            }
        }
        .media {
            display: flex;
            flex-flow: row wrap;
            justify-content: center;
            .soc-media {
                display: flex;
                flex-basis: 35%;
                justify-content: space-between;
                &__icon {
                    color: $light-blue;
                    &:hover {
                        color: $white;
                        transition: all 0.3s ease;
                    }
                }
            }
        }
    }
}
//
// POP-UP
//
#popup {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: $white;
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 2;
    .inner {
        position: relative;
        padding: rem(100) rem(80);
        .close {
            position: absolute;
            top: 5%;
            right: 10%;
            color: $yellow;
            &:hover {
                color: $light-yellow;
                cursor: pointer;
            }
        }
        .popupbody {
            display: flex;
            flex-flow: row wrap;
            .media {
                flex-basis: 60%;
                padding-right: rem(130);
                .popup__img {
                    display: flex;
                    flex-direction: column;
                    height: 100%;
                    background-color: #e6e6e6;
                    .picture__wrapper {
                        flex-grow: 1;
                        display: flex;
                        justify-content: center;
                    }
                }
                .navigations {
                    margin: 0 auto;
                    width: 10%;
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: rem(15);
                    a {
                        color: $black;
                        transition: all 0.3s ease;
                        &:hover {
                            color: $gray;
                        }
                    }
                }
                .count__wrapper {
                    text-align: center;
                    .count {
                        font-family: $font-lekton-italic;
                        font-weight: 900;
                        color: #362f2f;
                    }
                }
                .reaction {
                    display: flex;
                    width: 50%;
                    margin: 0 auto;
                    justify-content: space-between;
                    padding: rem(50);
                    &__links {
                        color: $light-blue;
                        transition: all 0.3s ease;
                        &:hover {
                            color: $gray;
                        }
                    }
                    .like--red {
                        color: $red;
                    }
                }
            }
            .content {
                flex-basis: 23%;
                background-color: $dark-blue;
                padding: rem(90) rem(20) rem(20) rem(20);
                font-family: $font-lekton-regular;
                &__main {
                    color: $white;
                    text-transform: uppercase;
                    margin-bottom: rem(15);
                    span {
                        color: $light-yellow;
                        cursor: text;
                    }
                }
                &__text {
                    line-height: 14px;
                    margin-bottom: rem(70);
                    p {
                        color: #362f2f;
                        white-space: pre-line;
                    }
                }
            }
            .nav {
                flex-basis: 17%;
                display: flex;
                flex-flow: row wrap;
                justify-content: center;
                align-content: center;
                &__li {
                    font-family: $font-lekton-regular;
                    text-transform: uppercase;
                    color: $light-blue;
                    margin-bottom: rem(10);
                    transition: all 0.3s ease;
                    &:hover {
                        color: $gray;
                    }
                }
            }
        }
    }
}
